<template>
    <div class="Vuex">
        <h1>{{this.$store.state.ma}}</h1>
        <h2>{{this.$store.getters.getStateCount}}</h2>

        <div>ma的值：{{this.$store.state.ma}}</div>
        <button @click="addjia()">+</button>
        <button @click="addjian()">-</button>
    </div>
</template> 
<script>
import {mapState,mapActions,mapGetters} from "vuex"
export default {
   data() {
      return {

      }
   },
   created() {

   },
   methods: {
       addjia(){
           this.$store.dispatch("addjia");
       },
       addjian(){
           this.$store.dispatch("addjian")
       }
   }
}
</script>

<style scoped>

</style>


